const Card = (props) => (
  // const position = {porps.handPosition}
  <div id={"pos" + props.handPosition}  className="cardTopLevel">
    <div className="topLIneContainer">
      <p>Card Title</p>
      <p>Mana Cost</p>
    </div>
    <p className="cardType">Card Type</p>
    <p>Description | Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec qu</p>
    <div className="powerAndToughnessContainer">
      <div className="powerHolder">
        <p>P: </p>
        <input type="number" value="42" />
      </div>
      <div className="toughnessHolder">
        <p>T: </p>
        <input type="number" value="42" />
      </div>
      <p className="handposition">{props.handPosition}</p>
    </div>
    <style jsx>{`
      .cardTopLevel {
        position:relative;
        border:1px solid black;
        max-width:200px;
        max-height:250px;
        background:#dedede;
        padding:25px;
      }
      .topLIneContainer {
        display:flex;
        justify-content:space-between;
      }
      .cardType {
        text-align:right;
      }
      .powerAndToughnessContainer {
        display:flex;
        justify-content:flex-end
      }
      .powerAndToughnessContainer > :nth-child(N+1) {
        display:flex;
      }
      input{
        max-width:3.5em;
      }
      .handposition {
        position:absolute;
        top:0;
        left:0;
        color:red;
      }
      #pos1, #pos2, #pos3, #pos4, #pos5, #pos6, #pos7, #pos8, #pos9, #pos10 {
        transition:500ms;
        opacity:.8;
      }
      #pos1:hover, #pos2:hover, #pos3:hover, #pos4:hover, #pos5:hover, #pos6:hover, #pos7:hover, #pos8:hover, #pos9:hover, #pos10:hover {
        z-index:999;
        opacity:1;
      }
      #pos2, #pos3, #pos4, #pos5, #pos6, #pos7, #pos8, #pos9, #pos10 {
        margin-left: -175px;
      }
      #pos2 {
        z-index:2;
      }
      #pos3 {
        z-index:3;
      }
      #pos4 {
        z-index:4;
      }
      #pos5 {
        z-index:5;
      }
      #pos6 {
        z-index:6;
      }
      #pos7 {
        z-index:7;
      }
      #pos8 {
        z-index:8;
      }
      #pos9 {
        z-index:9;
      }
      #pos10 {
        z-index:10;
      }
    `}</style>
  </div>
)

export default Card



